<template>
  <div class="bill">
      <div class="bill-top">
          <div class="bill-top-logo">
              <h1>爱琦记账本</h1>
          </div>
          <div class="bill-top-details">
            <div class="bill-top-details-date">
                2019年 <br><font>01月</font>
            </div>
            <div class="bill-top-details-export">
                收入<br><font>0.00</font>
            </div>
            <div class="bill-top-details-import">
                支出<br><font>777.00</font>
            </div>
          </div>    
      </div>
      <div class="bill-detail">
          <div class="bill-detail-top">
          </div>
          <div class="bill-detail-body">
              <van-list
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
                >
                <van-cell
                    v-for="item in list"
                    :key="item"
                    :title="item"
                />
               </van-list>
          </div>
           <div class="bill-detail-top">
          </div>
      </div>
      <div class="bill-Record">
          <font>记账</font>
      </div>
  </div>
</template>
<script>
import {List    } from 'vant';
export default {
  name: 'alibabaPay',
  components: {
      [List.name]: List
  },
  data() {
    return {
      list: [1,2,3],
      loading: false,
      finished: false
    };
  },
  methods: {
       onLoad() {
      // 异步更新数据
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 500);
    }
 
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
html,body{
	height: 100%;
	width: 100%;
}
#app{
    color: rgb(30, 41, 61);
    height: 100%;
    overflow: hidden;
     background: #BEBECA;
}
.bill{
    display: flex;
    flex-direction: column; 
    height: 100%;
    &-top{
        display: flex;
        flex-direction: column; 
        height: 20%;
        background: #FF69B4;
        &-logo{
            height: 45%;    
            text-align: center;
            line-height: 210%;
        }
        &-details{
            flex-grow:1;
            //background: black;
            display: flex;
            flex-direction: row;
            font{
                font-weight: bold;
                font-size: 23px;
            }
            &-date{
                padding-left: 5%;
                padding-top: 2%;
                width: 20%;
                //background: brown;
            }
            &-import{
                padding-left: 5%;
                padding-top: 2%;
                width: 40%;
                //background:cadetblue;
            }
            &-export{
                padding-left: 5%;
                padding-top: 2%;
                width: 40%;
                //background:cornflowerblue;
            }
        }
    }
    &-detail{
    display: flex;
    flex-direction: row;
      flex-grow:35;
        &-top{
            height: 20px;
             flex-grow:1;
            background: #FF69B4;
        }
        &-body{
            flex-grow:20;
            background: white;
            border-top: yellow 2px solid;
        }
    }
    &-Record{
         flex-grow:1;
         background: #FF69B4;
         text-align: center;
         line-height: -10%;
         font{
             font-size: 40px;
             font-weight: bold;
         }
    }
}

</style>
